@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  min-width: 320px;
  color: #011c40;
  background-color: #f9fbfd;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 150%;
}

body.is-active {
  overflow: hidden;
  background-color: rgba(0, 43, 102, 0.5);
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

::-webkit-scrollbar { /* chrome based */
  width: 0px; /* ширина scrollbar'a */
  background: transparent; /* опционально */
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
}

.main-page {
  flex-grow: 1;
}

.main-page__wrap {
  background-image: url("../img/big-main-background.png");
  background-repeat: no-repeat;
  background-size: cover;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  min-height: 650px;
  width: 100%;
  background-color: #07316f;
}
@media (max-width: 1023px) {
  .main-page__wrap {
    min-height: 638px;
    background-image: url("../img/small-main-background.png");
    filter: none;
  }
}
@media (max-width: 767px) {
  .main-page__wrap {
    min-height: 500px;
    background-image: url("../img/big-main-background.png");
  }
}

.main-page__container {
  color: #f9fbfd;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 270px 198px 0;
}
@media (max-width: 1023px) {
  .main-page__container {
    padding: 268px 45px 30px;
  }
}
@media (max-width: 767px) {
  .main-page__container {
    padding: 177px 15px 25px;
  }
}

.main-page__container h1 {
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  margin: 0;
  margin-bottom: 13px;
}
@media (max-width: 1023px) {
  .main-page__container h1 {
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .main-page__container h1 {
    font-size: 36px;
    line-height: 45px;
    margin-bottom: 10px;
  }
}

.main-page__container p {
  font-size: 20px;
  line-height: 24px;
  margin: 0;
}
@media (max-width: 1023px) {
  .main-page__container p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .main-page__container p {
    font-size: 16px;
    line-height: 22px;
    width: 70%;
  }
}

.main-page__info {
  padding: 120px 103px 145px;
}
@media (max-width: 1200px) {
  .main-page__info {
    padding: 110px 73px 118px;
  }
}
@media (max-width: 1023px) {
  .main-page__info {
    padding: 95px 45px 92px;
  }
}
@media (max-width: 767px) {
  .main-page__info {
    padding: 61px 15px 86px;
  }
}

.main-header {
  padding: 35px 103px 35px;
  position: absolute;
  top: 0;
  z-index: 10;
  width: 100%;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .main-header {
    padding: 28px 45px 30px 45px;
  }
}
@media (max-width: 767px) {
  .main-header {
    padding: 21px 15px 25px;
  }
}

@media (max-width: 767px) {
  .main-header.nojs {
    position: static;
    background-color: #07316f;
    padding: 0;
  }
}

.main-header__container {
  max-width: 1160px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .main-header__container {
    position: relative;
  }
}

.main-header__logo svg {
  fill: #f9fbfd;
  display: inline;
}
@media (max-width: 1023px) {
  .main-header__logo svg {
    width: 38px;
    height: 20px;
  }
}

@media (max-width: 767px) {
  .main-header__nav {
    position: absolute;
    top: -25px;
    right: -15px;
    width: 99%;
  }
}

@media (max-width: 767px) {
  .main-header__logo.is-active svg {
    position: absolute;
    top: 0;
    left: 13%;
    fill: black;
    z-index: 25;
  }
}

@media (max-width: 767px) {
  .main-header__nav.nojs {
    position: static;
    width: auto;
  }
}

.main-header__menu-overlay {
  display: none;
}

@media (max-width: 767px) {
  .main-header__menu-overlay.is-active {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 43, 102, 0.5);
    cursor: pointer;
  }
}

.navigation {
  max-width: 600px;
}

.navigation__button {
  display: none;
}
@media (max-width: 767px) {
  .navigation__button {
    display: block;
    position: absolute;
    top: 20px;
    right: 12px;
    width: 30px;
    height: 30px;
    cursor: pointer;
    border: none;
    background-color: transparent;
    z-index: 12;
    background-image: url("../img/svg/open-button.svg");
    background-repeat: no-repeat;
    background-position: center;
    width: 24px;
    height: 20px;
    padding: 15px;
  }
}

.navigation__button.is-active {
  background-image: url("../img/svg/close-button.svg");
  background-repeat: no-repeat;
  background-position: center;
  width: 16px;
  height: 16px;
  padding: 15px;
  top: 20px;
  right: 8px;
}
.navigation__button.is-active:hover, .navigation__button.is-active:active {
  background-color: #ec9913;
}

.navigation__button.nojs {
  display: none;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navigation__item {
  margin-right: 34px;
}
.navigation__item:last-child {
  margin-right: 0;
}
@media (max-width: 1023px) {
  .navigation__item {
    margin-right: 24px;
  }
}
@media (max-width: 767px) {
  .navigation__item {
    margin-bottom: 10px;
  }
}

.navigation__item a {
  color: #f9fbfd;
  font-weight: 500;
  line-height: 20px;
}
.navigation__item a:hover, .navigation__item a:focus {
  color: #ec9913;
}
@media (max-width: 1023px) {
  .navigation__item a {
    font-size: 12px;
    line-height: 14px;
  }
}

.about-trip__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 40px;
  align-items: center;
}
@media (max-width: 1023px) {
  .about-trip__container {
    column-gap: 30px;
    align-items: flex-start;
  }
}
@media (max-width: 767px) {
  .about-trip__container {
    display: flex;
    flex-direction: column;
    gap: 43px;
  }
}

.about-trip__information {
  padding-right: 25px;
}
@media (max-width: 1023px) {
  .about-trip__information {
    padding-right: 0;
  }
}

.about-trip__container h2 {
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  margin: 0;
  margin-top: -5px;
}
@media (max-width: 1023px) {
  .about-trip__container h2 {
    max-width: 240px;
    margin-bottom: 18px;
    font-size: 26px;
    line-height: 36px;
  }
}
@media (max-width: 767px) {
  .about-trip__container h2 {
    font-size: 24px;
    line-height: 34px;
    margin-bottom: 16px;
    max-width: none;
    width: 70%;
  }
}

.about-trip__container p {
  margin: 0;
  margin-top: 25px;
}
@media (max-width: 1023px) {
  .about-trip__container p {
    margin: 0;
  }
}

@media (max-width: 1023px) {
  .about-trip__container img {
    margin-top: 5px;
    margin-left: -5px;
  }
}
@media (max-width: 767px) {
  .about-trip__container img {
    margin-top: 0;
    margin-left: 0;
  }
}

.advantages {
  background-image: url("../img/advantages-background.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  min-height: 562px;
  background-color: #b3d9ed;
}
@media (min-resolution: 2dppx) {
  .advantages {
    background-image: url("../img/advantages-background@2x.webp");
  }
}

.advantages__container {
  padding: 110px 103px 118px;
  text-align: center;
}
@media (max-width: 1200px) {
  .advantages__container {
    padding: 110px 73px 118px;
  }
}
@media (max-width: 1023px) {
  .advantages__container {
    padding: 90px 45px 100px;
  }
}
@media (max-width: 767px) {
  .advantages__container {
    padding: 56px 15px 65px;
  }
}

.advantages__container h2 {
  font-size: 28px;
  line-height: 42px;
  font-weight: 600;
  margin: 0;
  margin-bottom: 50px;
}
@media (max-width: 1023px) {
  .advantages__container h2 {
    font-size: 26px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
  .advantages__container h2 {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 30px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 40px;
}
@media (max-width: 1023px) {
  .advantages__list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .advantages__list {
    flex-direction: column;
  }
}

.advantages__item {
  background-color: #f9fbfd;
  padding: 30px 30px 45px;
  text-align: left;
  width: 79%;
}
@media (max-width: 1023px) {
  .advantages__item {
    width: 80%;
    padding: 25px 15px 20px 20px;
  }
}
@media (max-width: 767px) {
  .advantages__item {
    width: 100%;
    padding: 25px 20px 20px;
  }
}

.advantages__img {
  margin-bottom: 12px;
}
@media (max-width: 1023px) {
  .advantages__img {
    height: 26px;
    margin-bottom: 12px;
  }
  .advantages__img--cruise {
    width: 21px;
  }
  .advantages__img--ticks {
    width: 27px;
  }
  .advantages__img--boat {
    width: 36px;
  }
}
@media (max-width: 767px) {
  .advantages__img {
    margin-bottom: 12px;
  }
}

.advantages__item h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  margin: 0;
  margin-bottom: 8px;
}
@media (max-width: 1023px) {
  .advantages__item h3 {
    line-height: 25px;
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .advantages__item h3 {
    max-width: 170px;
  }
}

.advantages__item p {
  font-size: 14px;
  line-height: 20px;
  margin: 0;
}
@media (max-width: 1023px) {
  .advantages__item p {
    margin: 0;
  }
}
.catalog {
  padding: 85px 118px 118px;
}
