  .catalog {
    padding: 110px 73px 118px;
  }
}
@media (max-width: 1147px) {
  .catalog {
    padding: 110px 45px 118px;
  }
}
@media (max-width: 1023px) {
  .catalog {
    padding: 90px 60px 100px;
  }
}
@media (max-width: 767px) {
  .catalog {
    padding: 56px 25px 65px;
  }
}

.catalog__container h2 {
  margin: 0;
  margin-bottom: 50px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
}
@media (max-width: 1023px) {
  .catalog__container h2 {
    font-size: 26px;
    line-height: 39px;
    margin-bottom: 48px;
  }
}
@media (max-width: 767px) {
  .catalog__container h2 {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 30px;
  }
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  width: 100%;
  margin: 0 auto;
}
@media (max-width: 1280px) {
  .catalog__list {
    gap: 20px;
  }
}
@media (max-width: 1023px) {
  .catalog__list {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .catalog__list {
    display: flex;
    flex-direction: column;
  }
}

.catalog__more-info {
  display: none;
}

